.App {
  text-align: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

body{
  background-color: dodgerblue;
}
input{
  margin: 20px;
 max-width: 230px;
 min-height: 30px;
  outline: none;
  font-size: 20px;
  font-family: 'Times New Roman', Times, serif;
  padding: 5px;
  border: none;
  outline: none;
  border-radius: 5px;
}
.strik{
  text-decoration: line-through;
  color: lightgray;
}

#btn {
width: 60px;
height: 30px;
border-radius: 5px;
outline: none;
border: none;
background-color: yellow;
font-family: 'Times New Roman', Times, serif;
font-weight: bold;
font-size: 15px;
}

.list-box {
  width: 300px;
  margin: 50px auto;
 
}
.todo-item{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  background-color: lightseagreen;
  color: #FFF;
  font-size: 20px;
  font-weight: bold;
  border-radius: 5px;
  font-family: 'Times New Roman', Times, serif;
  padding: 10px;
  margin-top: 10px;
}

button{
  background-color:lightseagreen ;
  border: none;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  width: 40px;
  height: 40px;
  
}
button:hover{
  background-color: #FFF;
  border-radius: 50%;
}




